const $header = $("#main-header"),
      $body = $("body"),
      $window = $(window),
      $burgerMenu = $("#burger-menu"),
      $burgerBtn = $("#burger-btn");
let currentTimeout;

function burgerMenuInit() {
    let isActive = false;
    
    function deactivate() {
        $burgerBtn.removeClass("active");
        $burgerMenu.removeClass("active");
        $body.removeClass("hidden-scroll");
        isActive = false;
    }

    function activate() {
        $burgerBtn.addClass("active");
        $burgerMenu.addClass("active");
        $body.addClass("hidden-scroll");
        $header.addClass("header--fixed");
        isActive = true;
    }

    $burgerBtn.on("click", (event) => {
        event.stopPropagation();

        if ( isActive ) {
            deactivate();
        } else {
            activate();
        }

        return false;
    });
}

function headerFixedTriggerInit() {    
    function triggerFixedHeader() {
        if ($window.scrollTop() > 150) {
            $header.addClass("header--fixed");
        } else {
            $header.removeClass("header--fixed");
        }
    }
    
    $window.on("scroll", triggerFixedHeader);
    triggerFixedHeader();
}

export default () => {
    burgerMenuInit();
    headerFixedTriggerInit();
};


