export default () => {
    let $newsList = $('#news-list'),
        itemsLength = $newsList.children().length,
        itemHeight = 36,
        animationTimeout = 5000,
        currentTransform = 0,
        i = 1;

    function showNextNewsItem() {
        if (i >= itemsLength) {
            i = 0;
        }
        currentTransform = itemHeight * i++;
        $newsList.css({ transform: `translate3d(0, -${currentTransform}px, 0)` })
    }

    if ($newsList && $newsList.length && (itemsLength > 1)) {
        setInterval(showNextNewsItem, animationTimeout);
    }
}


